body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.navbar {
  background-color: #6b0aeb;
  color: white;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar .links a {
  color: white;
  margin: 0 1rem;
  text-decoration: none;
}

.dashboard, .form-container, .student-list, .student-details {
  margin: 2rem;
}

form {
  display: flex;
  flex-direction: column;
  width: 300px;
}

form input, form button {
  margin: 0.5rem 0;
  padding: 0.5rem;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin: 1rem 0;
}
